	bottom: 0;
	cursor: pointer;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
	-webkit-transition: 0.4s;
	transition: 0.4s;
}
.btn-slider:before {
	background-color: #fff;
	bottom: 3px;
	content: "";
	height: 16px;
	left: 4px;
	position: absolute;
	-webkit-transition: 0.4s;
	transition: 0.4s;
	width: 16px;
}
